// Font Sizes
.fs12 {
    font-size: rem(12px);
}
.fs14 {
    font-size: rem(14px);
}
.fs18 {
    font-size: rem(18px);
}
.fs20 {
    font-size: rem(20px);
}
.fs24 {
    font-size: rem(24px);
}
.fs28 {
    font-size: rem(28px);
}
.fs30 {
    font-size: rem(30px);
}

.hidden {
    display: none;
    // z-index: 1;
}
.shown {
    display: block;
}
.relative {
    position: relative;
}
.absolute {
    position: absolute;
}

.red {
    background-color: red;
}
.blue {
    background-color: blue;
}
// cleaners
.clean-link {
    text-decoration: none;
    color: inherit;
}

.clean-list {
    list-style: none;
    padding: 0;
    margin: 0;
}

// flex helpers

.flex {
    display: flex;

    > *.full {
        flex: 1;
    }
    &.column {
        flex-direction: column;
    }
    &.row {
        flex-direction: row;
    }
    &.justify-center {
        justify-content: center;
    }
    &.align-center {
        align-items: center;
    }
    &.align-end {
        align-items: flex-end;
    }
    &.space-between {
        justify-content: space-between;
    }
    &.space-evenly {
        justify-content: space-evenly;
    }
    &.wrap {
        flex-wrap: wrap;
    }
    &.grid {
        display: grid;
    }
}

// Media helpers

.square-ratio {
    @include aspect-ratio(1, 1);
}
.video-ratio {
    @include aspect-ratio(16, 9);
}
.portrait-ratio {
    @include aspect-ratio(9, 16);
}
